import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ICommand, RxCommandFactoryService } from '@helix/platform/shared/api';

@Injectable({
  providedIn: 'root'
})
export class RxWebApiDefinitionService {
  constructor(private rxCommandFactoryService: RxCommandFactoryService) {}

  rename(oldWebApiDefinitionName: string, newWebApiDefinitionName: string): Observable<any> {
    const renameCommand: ICommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.webapi.command.RenameWebApiDefinitionCommand'
    );

    return renameCommand.execute({
      name: oldWebApiDefinitionName,
      newName: newWebApiDefinitionName
    });
  }
}
